/**
 * Section methods
 * Interaction between theme settings and the theme editor
 * -----------------------------------------------------------------------------
 *
 * @namespace section
 */

import Flickity from 'flickity';

var sectionMethods = {

  methods: {
    _initSection: function _initSection() {
      $(document).on('shopify:section:load', app._onSectionLoad.bind(this)).on('shopify:section:unload', app._onSectionUnload.bind(this)).on('shopify:section:select', app._onSelect.bind(this)).on('shopify:section:deselect', app._onDeselect.bind(this)).on('shopify:block:select', app._onBlockSelect.bind(this)).on('shopify:block:deselect', app._onBlockDeselect.bind(this));
    },
    _onSectionLoad: function _onSectionLoad(e) {
      var sectionId = e.detail.sectionId;
    },
    _onSectionUnload: function _onSectionUnload(e) {
      var sectionId = e.detail.sectionId;
    },
    _onSelect: function _onSelect(e) {
      var sectionId = e.detail.sectionId;
      var searchType = $(app.selectors.searchLayer).data('search-type');

      switch (sectionId) {
        case 'mobile-navigation':
          {
            $('.mobile-nav-drawer').addClass('opened');
            if (!app.isMobileNavDrawerOpen) {
              app._toggleMobileNavDrawer();
            }
            break;
          }
        case 'newsletter-popup':
          {
            $('.newsletter-popup').addClass('opened');
            if (!app.isNewsletterPopupOpen) {
              app._toggleNewsletterPopup();
            }
            break;
          }
        case 'search-layer':
          {
            if (searchType === 'full_screen') {
              app.isOverlayVisible = false;
              $('.search-full-screen').addClass('opened');
            } else {
              app.isOverlayVisible = true;
              $('.search-reveal').addClass('opened');
            }
            if (!app.isSearchLayerOpen) {
              app._toggleSearch();
            }
            break;
          }
        default:
          {
            var $carousel = $(app.selectors.carousel);

            if ($carousel.length === 0) {
              return;
            }

            var options = $carousel.data('flickity');
            var flkty = new Flickity($carousel[0], options);
            setTimeout(function () {
              flkty.resize();
            }, 100);

            break;
          }
      }
    },
    _onDeselect: function _onDeselect(e) {
      var sectionId = e.detail.sectionId;

      switch (sectionId) {
        case 'mobile-navigation':
          {
            $('.mobile-nav-drawer').removeClass('opened');
            if (app.isMobileNavDrawerOpen) {
              app._toggleMobileNavDrawer();
            }
            break;
          }
        case 'newsletter-popup':
          {
            $('.newsletter-popup').removeClass('opened');
            if (app.isNewsletterPopupOpen) {
              app._toggleNewsletterPopup();
            }
            break;
          }
        case 'search-layer':
          {
            app.isOverlayVisible = false;
            $('header, footer, section').removeClass('filter-blur').css('opacity', '1');
            $('.search-full-screen, .search-reveal').removeClass('opened');
            if (app.isSearchLayerOpen) {
              app._toggleSearch();
            }
            break;
          }
        default:
          {
            break;
          }
      }
    },
    _onReorder: function _onReorder(e) {
      var sectionId = e.detail.sectionId;
    },
    _onBlockSelect: function _onBlockSelect(e) {
      var sectionId = e.detail.sectionId;
      var blockId = e.detail.blockId;

      switch (sectionId) {
        case 'index-header-slider':
          {
            var $carousel = $(app.selectors.carousel);
            var flkty = Flickity.data($carousel[0]);
            var cellElements = flkty.getCellElements();

            flkty.pausePlayer();

            cellElements.forEach(function (element, index) {
              var cellId = $(element).data('block-id');

              if (parseFloat(blockId) === parseFloat(cellId)) {
                flkty.selectCell(index, true, true); // value, isWrapped, isInstant
              }
            });
            break;
          }
        default:
          {
            break;
          }
      }
    },
    _onBlockDeselect: function _onBlockDeselect(e) {
      var sectionId = e.detail.sectionId;
    }
  }
};

export default sectionMethods;